import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import pageERR from "../views/404.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/pageERR",
    name: "pageERR",
    component: pageERR
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
